* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

div {
  padding: 30px 0;
}

li {
  list-style-type: none;
}

form {
  width: 300px;
  margin: 0 auto;
  position: relative;
}

.mint {
  background: #a3d0c3;
}

input {
  width: 100%;
  height: 42px;
  color: #9e9c9c;
  background: #f9f0da;
  border: 2px solid #7ba7ab;
  border-radius: 5px;
  outline: none;
  padding-left: 10px;
}

.search {
  width: 42px;
  height: 42px;
  cursor: pointer;
  background: #7ba7ab;
  border: none;
  border-radius: 0 5px 5px 0;
  position: absolute;
  top: 0;
  right: 0;
}

.search:before {
  content: "ï€‚";
  color: #f9f0da;
  font-family: FontAwesome;
  font-size: 16px;
}

.is-hidden {
  display: none;
}

.photo-grid {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
}

.photo-card {
  max-width: 300px;
  max-height: 500px;
  border: 1px solid #ddd;
  grid-template-rows: auto auto 1fr;
  padding: 10px;
  display: grid;
}

.photo-card img {
  object-fit: cover;
  width: 100%;
  height: 200px;
}

.info {
  color: #333;
  text-align: center;
  background-color: #f9f0da;
  border: 1px solid #9e9c9c;
  border-radius: 5px;
  margin-top: 10px;
  padding: 10px;
  font-size: 16px;
  font-weight: bold;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.container {
  text-align: center;
  background: linear-gradient(190deg, #ed86e2, #1b9dab, #90ddf6);
  margin: 15px;
  padding: 15px;
}

.load-more {
  width: 200px;
  color: #333;
  text-align: center;
  background-color: #a3d0c3;
  border: 1px solid #ddd;
  border-radius: 10px;
  justify-content: center;
  margin: 0 auto;
  padding: 15px;
}

